.header {
  background-image: url(../images/header.jpg);
  background-size: cover;
  background-position: center;
  

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    box-sizing: border-box;
    height: 100vh;
    padding: $headerTopPadding 0;
  }

  &__top {
    @include top-actions;
  }

  &__title {
    font-weight: bold;
    font-size: 140px;
    line-height: 148px;
    color: #fff;
    letter-spacing: -1px;

    @include onDesktop {
      font-size: 80px;
      line-height: 80px;
      letter-spacing: -1.5px;
    }

    &::before {
      content: "";
      display: block;
      width: 110px;
      height: 5px;
      background-color: #fff;
      margin-bottom: 24px;

      @include onDesktop {
        width: 40px;
        height: 2px;
        border-radius: 2px;
      }
    }
  }
}
